import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: () => import('../views/LoginView.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('../views/RegisterView.vue')
  },
  {
    path: '/user',
    name: 'UserView',
    component: () => import('../views/UserView.vue'),
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: '',
        name: 'UserDashboard',
        component: () => import('../components/user_components/UserDashboard.vue'),
      },
      {
        path: 'tryout',
        name: 'UserTryoutDetail',
        component: () => import('../components/user_components/UserTryoutDetail.vue'),
      },
      {
        path: 'purchase',
        name: 'UserPurchaseTryout',
        component: () => import('../components/user_components/UserPurchaseTryout.vue'),
      },
      {
        path: 'profile',
        name: 'UserProfile',
        component: () => import('../components/user_components/UserProfile.vue'),
      },
      {
        path: 'statistic',
        name: 'UserStatistic',
        component: () => import('../components/user_components/UserStatistic.vue'),
      },
    ]
  },
  {
    path: '/admin',
    name: 'AdminView',
    component: () => import('../views/AdminView.vue'),
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: '',
        name: 'AdminDashboard',
        component: () => import('../components/admin_components/AdminDashboard.vue'),
      },
      {
        path: 'profile',
        name: 'AdminProfile',
        component: () => import('../components/admin_components/AdminProfile.vue'),
      },
      {
        path: 'manage-tryout',
        name: 'AdminManageTryout',
        component: () => import('../components/admin_components/AdminManageTryout.vue'),
      },
    ]
  },
  {
    path: '*',
    name: 'Fallback',
    component: () => import('../views/404View.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('userData') == null) {
      next({
        name: 'Login'
      })
    } else {
      next();
    }
  } else {
    next();
  }
})

export default router
