import UserService from '../services/userService';

const initialProfileState = { userProfileData: null };

export const user = {
    namespaced: true,
    state: initialProfileState,
    actions: {
        getDetail({ commit }, userId) {
            return UserService.detail(userId)
                .then(
                    response => {
                        commit('getDetailSuccess', response.data.detail);
                        return Promise.resolve(response);
                    },
                    error => {
                        commit('getDetailFailed');
                        return Promise.reject(error);
                    }
                )
        },
        editProfile({ commit }, data) {
            return UserService.editProfile(data)
                .then(
                    response => {
                        commit('updateSuccess');
                        return Promise.resolve(response);
                    },
                    error => {
                        return Promise.reject(error);
                    }
                )
        },
        editPassword({ commit }, data) {
            return UserService.editPassword(data)
                .then(
                    response => {
                        commit('editPasswordSuccess');
                        return Promise.resolve(response);
                    },
                    error => {
                        return Promise.reject(error);
                    }
                )
        },
        clearState({ commit }) {
            commit('onClearState');
        }
    },
    mutations: {
        getDetailSuccess(state, detail) {
            state.userProfileData = detail;
        },
        getDetailFailed(state) {
            state.userProfileData = null;
        },
        updateSuccess() {
        },
        editPasswordSuccess() {
        },
        clearState(state) {
            state.userProfileData = null;
        }
    },
    getters: {
        userProfileData: (state) => {
            return state.userProfileData;
        }
    }
}