import axios from 'axios';
const baseUrl = "http://localhost:3000/api";

class UserService {
    detail(userId) {
        return axios.get(`${baseUrl}/detail/${userId}`);
    }

    editProfile(data) {
        return axios.put(`${baseUrl}/edit`, data);
    }

    editPassword(data) {
        return axios.put(`${baseUrl}/edit-password`, data);
    }
}

export default new UserService();