import AuthService from '../services/authService';

const user = JSON.parse(localStorage.getItem('userData'));
const initialState = user ? { status: { isLoggedIn: true }, user } : { status: { isLoggedIn: false }, user: null };

export const auth = {
    namespaced: true,
    state: initialState,
    actions: {
        login({ commit }, user) {
            return AuthService.login(user)
                .then(
                    response => {
                        commit('loginSuccess', response.data.userData);
                        return Promise.resolve(response);
                    },
                    error => {
                        commit('loginFailed');
                        return Promise.reject(error);
                    }
                )
        },
        register({ commit }, user) {
            return AuthService.register(user)
                .then(
                    response => {
                        commit('registerSuccess');
                        return Promise.resolve(response);
                    },
                    error => {
                        commit('registerFailed');
                        return Promise.reject(error);
                    }
                )
        },
        update({ commit }, updated) {
            AuthService.update(updated);
            commit('onProfileUpdated', updated);
        },
        logout({ commit }) {
            return AuthService.logout()
                .then(
                    response => {
                        commit('logoutSuccess')
                        return Promise.resolve(response);
                    },
                    error => {
                        return Promise.reject(error);
                    }
                );
        },
        clearAuthState({ commit }) {
            AuthService.clearAuth()
            commit('onClearAuthState');
        }
    },
    mutations: {
        loginSuccess(state, user) {
            state.status.isLoggedIn = true;
            state.user = user;
        },
        loginFailed(state) {
            state.status.isLoggedIn = false;
            state.user = null;
        },
        registerSuccess(state) {
            state.status.isLoggedIn = false;
            state.user = null;
        },
        registerFailed(state) {
            state.status.isLoggedIn = false;
            state.user = null;
        },
        logoutSuccess(state) {
            state.status.isLoggedIn = false;
            state.user = null;
        },
        onProfileUpdated(state, updated) {
            state.user = updated;
        },
        onClearAuthState(state) {
            state.status.isLoggedIn = false;
            state.user = null;
        }
    },
    getters: {
        userId: (state) => {
            return state.user.id;
        }
    }
}